
html, body{
  height: 100vh;
  width: 100vw;
}

.calendar-screen{
  display: flex;
  flex-flow: column;
  height: 100vh;
}

.rbc-calendar{
  height: 100vh;
  /* width: 100%; */
}

/* MODAL */

.ReactModalPortal > div{
  opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
  align-items: center;
  display: flex;
  justify-content: center;
  transition: opacity .2s ease-in-out;
  z-index: 999;
}

.modal-fondo {
  background-color: rgba(0, 0, 0, 0.3);
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  position: fixed;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close {
  opacity: 0;
}

.modal {
  background: white;
  border-radius: 5px;
  color: rgb(51, 51, 51);
  display: inline;
  max-height: 620px;
  max-width: 500px;
  outline: none;
  padding: 10px;
}


/* Datepicker */

.react-datetime-picker input:focus{
  outline: none;
}

.react-datetime-picker__wrapper{
  border: 0px solid gray !important;
}


/*Fabs*/

.fab{
  border-radius: 100%;
  bottom:15px;
  font-size: 15px;
  padding: 15px;
  position: fixed;
  right: 15px;
}

.fab-danger{
  bottom: 10px;
  position: fixed;
  left: 15px;
  background-color: #f44336;
  padding: 5px;
}